import React, { Component } from 'react';
import axios from 'axios';



class TaoThongBao extends Component{
    constructor(props){
        super(props);
        this.state = {
            tieuDe : '',
            noiDung : ''
        }
    }

    onChange = (e) => {
        const target = e.target;
        const name = target.name;
        const value = target.value;
        this.setState({
            [name] : value
        })
    }
    
    onSubmit = (e) => {
        e.preventDefault();
        const {history} = this.props;
        axios({
            method : 'POST',
            url : 'http://localhost:4000/tbs',
            data : {
                tieuDe: this.state.tieuDe,
                noiDung : this.state.noiDung,
            },
            withCredentials: true
        }).then(res => {
            if(res.data){
                alert('To thng bo thnh cng');
                history.push('/LuaChon');
            }
            else alert('C li xy ra, xin th li');
        })
    }
  render() {
    return (
        <div className="row">
            <div className="col-xs- col-sm- col-md-6 col-lg- ml"> 
                <div className="panel panel-danger ml mt" style={{boxShadow: '0 0 10px rgba(0,0,0,1)'}}>
                    <div className="panel-heading">
                            <h3 className="panel-title mc" style={{fontSize: '30px'}}>To thng bo</h3>
                    </div>
                    <div className="panel-body" style={{margin:'15px'}}>
                        <form onSubmit={this.onSubmit}>
                            <div className="form-group">
                                <label>Tiu </label>
                                <input type="text" className="form-control" name="tieuDe" placeholder="Tiu " onChange={this.onChange}/>
                            </div>
                            <div className="form-group">
                                <label>Ni dung</label>
                                <textarea type="text" className="form-control" name="noiDung" placeholder="Ni dung"  onChange={this.onChange}/>
                            </div>            
                            <button type="submit" className="btn btn-success" style={{width: "100px", marginLeft: "42%"}}>To</button>
                        </form> 
                    </div>
                </div>
            </div>  
        </div>
    );
  }
  
}

export default TaoThongBao;