import React, { Component } from 'react';
import axios from 'axios';



class ThemDoAn extends Component{
    constructor(props){
        super(props);
        this.state = {
            tenDoAn : '',
            nenTang : '',
            loai : ' n c s',
            moTa : '',
            ngDK : '',
        }
    }

    onChange = (e) => {
        const target = e.target;
        const name = target.name;
        const value = target.value;
        this.setState({
            [name] : value
        })
    }
    
    onSubmit = (e) => {
        e.preventDefault();
        const {history, match} = this.props;
        let {id} = match.params;
        e.preventDefault();
        axios({
            method : 'POST',
            url : `http://localhost:4000/topics/Them/${id}`,
            data : {
                tenDoAn : this.state.tenDoAn,
                nenTang : this.state.nenTang,
                loai : this.state.loai,
                moTa : this.state.moTa,
                ngDK : this.state.ngDK,
            },
            withCredentials: true
        }).then(res => {
            if(res.data){
                alert('Thm  n thng cng');
                history.push(`/QuanLyDoAn/${id}`);
            }
            else alert('C li xy ra, xin th li');
        })
    }
  render() {
    return (
        <div>
        <div className="center">    
        <div className="container"> 
            <div className="text">Thm  Ti</div>
            <form onSubmit={this.onSubmit}>
                <div className="data">
                    <label>Tn  ti</label>
                    <input type="text" placeholder="Nhp tn  ti" name="tenDoAn" value={this.state.tenDoAn}  required onChange={this.onChange}/>
                </div>
                <div className="data">
                    <label>Nn tng</label>
                    <input type="text" placeholder="Nhp nn tng" name="nenTang" value={this.state.nenTang} required onChange={this.onChange}/>
                </div>
                <div className="data">
                    <label>Loi  n</label>
                    <select className="form-control" name="loai" onChange={this.onChange} value={this.state.loai}>
                        <option value=" n c s"> n c s</option>
                        <option value=" n chuyn ngnh"> n chuyn ngnh</option>
                        <option value=" n tt nghip"> n tt nghip</option>
                        <option value="Kha lun">Kha lun</option>
                    </select>
                </div>
                <div className="data">
                    <label>M t</label>
                    <textarea type="text" placeholder="M t" name="moTa" value={this.state.moTa} onChange={this.onChange}/>
                </div>
                <div className="data">
                    <label>S lng</label>
                    <input type="number" min='1' max='3' className="form-control" name="ngDK" value={this.state.ngDK} onChange={this.onChange}/>
                </div>
                <div className="btn">
                    <div className="inner" />
                    <button className="submit-button" type="submit" >Thm</button>
                </div>
            </form>
        </div>
        </div>
    </div>
    );
  }
  
}

export default ThemDoAn;